import { Mail } from '@/app/mail/components/mail'
import { cookies } from 'next/headers'
import { defaultLayout as defaultLayoutConfig } from './data'

export default function MailPage() {
  // Try to get layout from cookies, fallback to defaultLayoutConfig
  const layout = cookies().get("react-resizable-panels:layout:mail")
  const collapsed = cookies().get("react-resizable-panels:collapsed")

  // Parse the layout from cookies or use default
  let parsedLayout: number[] | undefined = undefined
  
  try {
    if (layout?.value) {
      parsedLayout = JSON.parse(layout.value)
    }
  } catch (e) {
    console.error("Failed to parse layout from cookies", e)
  }

  // Fallback to default layout if parsing fails
  const defaultLayout = parsedLayout || defaultLayoutConfig || [20, 40, 40]
  const defaultCollapsed = collapsed ? JSON.parse(collapsed.value) : undefined

  return (
    <>
      <div className="md:hidden">
        <img
          src="/examples/mail-dark.png"
          width={1280}
          height={727}
          alt="Mail"
          className="hidden dark:block"
        />
        <img
          src="/examples/mail-light.png"
          width={1280}
          height={727}
          alt="Mail"
          className="block dark:hidden"
        />
      </div>
      <div className="flex-col hidden md:flex h-screen overflow-scroll">
        <Mail
          defaultLayout={defaultLayout}
          defaultCollapsed={defaultCollapsed}
          navCollapsedSize={4}
        />
      </div>
    </>
  )
}
